"use client";
import { useState, useEffect } from 'react';
import { useFinance } from '@/context/FinanceContext';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { Tabs, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Checkbox } from '@/components/ui/checkbox';
import { Save, ArrowRightLeft, CalendarClock, AlertTriangle, CheckCircle2, AlertCircle } from 'lucide-react';
import { DialogFooter, DialogHeader, DialogTitle, DialogDescription } from '@/components/ui/dialog';

interface Props {
  onClose?: () => void;
  defaultType?: 'income' | 'expense' | 'transfer';
}

// Funci칩n auxiliar para formatear dinero bonito (Ej: $ 1.250.000)
const formatMoney = (amount: number) => {
    return new Intl.NumberFormat('es-CO', { style: 'currency', currency: 'COP', minimumFractionDigits: 0 }).format(amount);
};

export default function TransactionForm({ onClose, defaultType = 'expense' }: Props) {
  const { addTransaction, accounts, categories, fixedRules } = useFinance();
  
  // ESTADOS DE PASOS: 'form' | 'confirm' | 'success'
  const [step, setStep] = useState<'form' | 'confirm'>('form');

  const [mode, setMode] = useState<'income' | 'expense' | 'transfer'>(defaultType);
  
  // Estados Generales
  const [amount, setAmount] = useState('');
  const [desc, setDesc] = useState('');
  const [date, setDate] = useState(new Date().toISOString().split('T')[0]);
  const [accountId, setAccountId] = useState('');
  const [categoryId, setCategoryId] = useState('');

  // L칩gica Recurrente
  const [isFixedMode, setIsFixedMode] = useState(false); 
  const [selectedRuleId, setSelectedRuleId] = useState(''); 
  const [daysLate, setDaysLate] = useState<number | null>(null); 

  // L칩gica Crear Nueva Regla
  const [createRuleFromTx, setCreateRuleFromTx] = useState(false);
  const [newRuleDay, setNewRuleDay] = useState<string>('1');

  // L칩gica Ahorro / Transfer
  const [isSavings, setIsSavings] = useState(false);
  const [didWithdraw, setDidWithdraw] = useState(true);
  const [targetAccountId, setTargetAccountId] = useState('');

  // Efectos (Mantener los mismos de c치lculo de fechas y reglas existentes)
  useEffect(() => {
    if (isFixedMode && selectedRuleId) {
        const rule = fixedRules.find(r => r.id === selectedRuleId);
        if (rule) {
            setAmount(rule.budgetedAmount.toString());
            setDesc(rule.description);
            setCategoryId(rule.categoryId);
            setAccountId(rule.accountIdToCharge);
        }
    }
  }, [selectedRuleId, isFixedMode, fixedRules]);

  useEffect(() => {
    if (isFixedMode && selectedRuleId) {
        const rule = fixedRules.find(r => r.id === selectedRuleId);
        if (rule) {
            const selectedDay = parseInt(date.split('-')[2]);
            const ruleDay = rule.dayOfMonth;
            if (selectedDay > ruleDay) setDaysLate(selectedDay - ruleDay);
            else setDaysLate(null);
        }
    } else {
        setDaysLate(null);
    }
  }, [date, selectedRuleId, isFixedMode, fixedRules]);


  // --- PASO 1: VALIDAR Y MOSTRAR CONFIRMACI칍N ---
  const handlePreSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!amount || !accountId) return;
    if (mode === 'transfer' && !targetAccountId) return;
    // Si todo est치 ok, pasamos a confirmar
    setStep('confirm');
  };

  // --- PASO 2: GUARDAR REALMENTE ---
  const handleFinalSubmit = async () => {
    const numAmount = parseFloat(amount);
    const safeDate = new Date(`${date}T12:00:00`).toISOString();
    const originAcc = accounts.find(a => a.id === accountId);
    const targetAcc = accounts.find(a => a.id === targetAccountId);

    if (mode === 'transfer') {
        await addTransaction({
            type: 'transfer', amount: numAmount, accountId, relatedAccountId: targetAccountId,
            originAccountName: originAcc?.name, destAccountName: targetAcc?.name,
            date: safeDate, description: desc || 'Transferencia'
        });
    } else {
        await addTransaction({
            type: mode, amount: numAmount, accountId, categoryId, description: desc,
            date: safeDate, isFixed: isFixedMode, isSavings: mode === 'expense' && isSavings,
            didWithdraw: mode === 'expense' && isSavings ? didWithdraw : true,
            createFixedRule: createRuleFromTx, dayOfMonth: parseInt(newRuleDay)
        });
    }
    
    // Feedback visual y cierre
    if (onClose) onClose();
  };

  // --- VISTA DE CONFIRMACI칍N ---
  if (step === 'confirm') {
      const numAmount = parseFloat(amount);
      return (
          <div className="space-y-6 animate-in zoom-in-95 duration-200">
              <div className="text-center space-y-2">
                  <div className="mx-auto w-12 h-12 bg-indigo-900/30 rounded-full flex items-center justify-center text-indigo-400">
                      <AlertCircle className="w-6 h-6" />
                  </div>
                  <DialogTitle className="text-xl text-white">쮺onfirmar Movimiento?</DialogTitle>
                  <DialogDescription className="text-zinc-400">Verifica los detalles antes de guardar.</DialogDescription>
              </div>

              <div className="bg-zinc-900 p-4 rounded-lg border border-zinc-800 space-y-3 text-sm">
                  <div className="flex justify-between items-center border-b border-zinc-800 pb-2">
                      <span className="text-zinc-500">Tipo:</span>
                      <span className={`font-bold uppercase ${mode === 'income' ? 'text-emerald-400' : mode === 'expense' ? 'text-rose-400' : 'text-blue-400'}`}>
                          {mode === 'income' ? 'Ingreso' : mode === 'transfer' ? 'Transferencia' : 'Gasto'}
                      </span>
                  </div>
                  <div className="flex justify-between items-center">
                      <span className="text-zinc-500">Monto:</span>
                      <span className="text-xl font-bold text-white">{formatMoney(numAmount)}</span>
                  </div>
                  <div className="flex justify-between items-center">
                      <span className="text-zinc-500">Fecha:</span>
                      <span className="text-zinc-300">{date}</span>
                  </div>
                  <div className="flex justify-between items-center">
                      <span className="text-zinc-500">Descripci칩n:</span>
                      <span className="text-zinc-300 max-w-[200px] truncate text-right">{desc || "Sin descripci칩n"}</span>
                  </div>
                  {/* Detalles extra */}
                  {createRuleFromTx && (
                      <div className="bg-indigo-900/20 text-indigo-300 p-2 rounded text-xs text-center mt-2 border border-indigo-500/20">
                          游댒 Se crear치 una regla recurrente mensual.
                      </div>
                  )}
                   {isFixedMode && daysLate !== null && (
                      <div className="bg-red-900/20 text-red-300 p-2 rounded text-xs text-center mt-2 border border-red-500/20">
                          丘멆잺 Pago con {daysLate} d칤as de retraso.
                      </div>
                  )}
              </div>

              <div className="flex gap-3">
                  <Button variant="outline" onClick={() => setStep('form')} className="flex-1 border-zinc-700 hover:bg-zinc-800 text-zinc-300">
                      Corregir
                  </Button>
                  <Button onClick={handleFinalSubmit} className={`flex-1 font-bold ${mode === 'income' ? 'bg-emerald-600 hover:bg-emerald-700' : 'bg-indigo-600 hover:bg-indigo-700'}`}>
                      <CheckCircle2 className="w-4 h-4 mr-2" /> Confirmar
                  </Button>
              </div>
          </div>
      );
  }

  // --- VISTA FORMULARIO (C칩digo original con el handleSubmit cambiado a handlePreSubmit) ---
  const filteredCategories = categories.filter(c => mode === 'income' ? c.type === 'income' : c.type !== 'income');

  return (
    <div className="space-y-4 text-white">
      <Tabs value={mode} onValueChange={(v:any) => { setMode(v); setIsSavings(false); setIsFixedMode(false); }} className="w-full">
        <TabsList className="grid w-full grid-cols-3 bg-zinc-800">
          <TabsTrigger value="income" className="data-[state=active]:bg-green-600">Ingreso</TabsTrigger>
          <TabsTrigger value="expense" className="data-[state=active]:bg-red-600">Gasto</TabsTrigger>
          <TabsTrigger value="transfer" className="data-[state=active]:bg-blue-600">Transferir</TabsTrigger>
        </TabsList>
      </Tabs>

      <form onSubmit={handlePreSubmit} className="space-y-4 mt-4">
        
        {/* ... (TODA LA L칍GICA DE INPUTS, SWITCHES Y SELECTS DEL CODIGO ANTERIOR SE MANTIENE IGUAL AQUI) ... */}
        {/* Solo aseg칰rate de copiar los inputs de la respuesta anterior aqu칤 dentro */}
        
        {mode === 'expense' && (
            <div className="bg-zinc-900/50 p-3 rounded-lg border border-zinc-800 space-y-3">
                 {/* ... Codigo Switch Recurrente ... */}
                 <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                        <CalendarClock className="h-4 w-4 text-indigo-400"/>
                        <Label className="cursor-pointer">쮼s un Gasto Recurrente?</Label>
                    </div>
                    <Switch checked={isFixedMode} onCheckedChange={setIsFixedMode} className="data-[state=checked]:bg-indigo-500"/>
                </div>
                 {/* ... Selector de Reglas ... */}
                 {isFixedMode && (
                    <div className="animate-in slide-in-from-top-2 pt-2 border-t border-zinc-800">
                        <Label className="text-xs text-zinc-400 mb-1 block">Seleccionar regla existente</Label>
                        <Select onValueChange={setSelectedRuleId} value={selectedRuleId}>
                            <SelectTrigger className="bg-zinc-950 border-zinc-700"><SelectValue placeholder="-- Selecciona pago --" /></SelectTrigger>
                            <SelectContent>
                                {fixedRules.map(rule => (
                                    <SelectItem key={rule.id} value={rule.id}>{rule.description} (D칤a {rule.dayOfMonth})</SelectItem>
                                ))}
                            </SelectContent>
                        </Select>
                         {/* Alerta de mora */}
                         {daysLate !== null && (
                            <div className="mt-3 bg-red-900/20 border border-red-500/50 p-3 rounded flex items-start gap-3">
                                <AlertTriangle className="h-5 w-5 text-red-500 shrink-0 mt-0.5" />
                                <div>
                                    <p className="text-sm font-bold text-red-400">춰Pago Retrasado!</p>
                                    <p className="text-xs text-red-200">Llevas <span className="font-bold underline">{daysLate} d칤as</span> de retraso.</p>
                                </div>
                            </div>
                        )}
                    </div>
                 )}
            </div>
        )}

        {/* Fecha y Monto */}
        <div className="space-y-1">
            <Label>Fecha</Label>
            <Input type="date" value={date} onChange={e => setDate(e.target.value)} className="bg-black border-zinc-700" />
        </div>
        <div className="space-y-2">
            <Label className="text-xl font-bold">Monto</Label>
            <Input type="number" step="0.01" placeholder="$0" required className="text-2xl font-bold bg-black border-zinc-700 h-14" value={amount} onChange={e => setAmount(e.target.value)} />
        </div>

        {/* Selectores Cuentas/Categorias */}
        {mode !== 'transfer' && (
             <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                   <Label>Cuenta</Label>
                   <Select onValueChange={setAccountId} value={accountId} required>
                       <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Seleccionar" /></SelectTrigger>
                       <SelectContent>{accounts.filter(a => !a.isHidden).map(acc => <SelectItem key={acc.id} value={acc.id}>{acc.name}</SelectItem>)}</SelectContent>
                   </Select>
                </div>
                <div className="space-y-2">
                   <Label>Categor칤a</Label>
                   <Select onValueChange={setCategoryId} value={categoryId} required>
                       <SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Seleccionar" /></SelectTrigger>
                       <SelectContent>{filteredCategories.map(cat => <SelectItem key={cat.id} value={cat.id}>{cat.name}</SelectItem>)}</SelectContent>
                   </Select>
                </div>
            </div>
        )}
        
        {/* Transferencia Selectores */}
        {mode === 'transfer' && (
             <div className="grid grid-cols-2 gap-4 bg-zinc-900 p-3 rounded-lg">
                <div className="space-y-2">
                    <Label>Desde</Label>
                    <Select onValueChange={setAccountId} required><SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Origen" /></SelectTrigger><SelectContent>{accounts.filter(a => !a.isHidden).map(acc => <SelectItem key={acc.id} value={acc.id}>{acc.name}</SelectItem>)}</SelectContent></Select>
                </div>
                <div className="space-y-2">
                    <Label>Hacia</Label>
                    <Select onValueChange={setTargetAccountId} required><SelectTrigger className="bg-zinc-800 border-zinc-700"><SelectValue placeholder="Destino" /></SelectTrigger><SelectContent>{accounts.filter(a => !a.isHidden && a.id !== accountId).map(acc => <SelectItem key={acc.id} value={acc.id}>{acc.name}</SelectItem>)}</SelectContent></Select>
                </div>
            </div>
        )}

        <div className="space-y-2">
            <Label>Descripci칩n</Label>
            <Input className="bg-zinc-800 border-zinc-700" value={desc} onChange={e => setDesc(e.target.value)} placeholder="Descripci칩n..." />
        </div>

        {/* Checkbox crear nueva regla */}
        {mode === 'expense' && !isFixedMode && (
            <div className="flex items-start gap-2 pt-2">
                <Checkbox id="create-rule" checked={createRuleFromTx} onCheckedChange={(c:any) => setCreateRuleFromTx(c)} className="mt-1 border-zinc-500 data-[state=checked]:bg-indigo-600 data-[state=checked]:border-indigo-600"/>
                <div className="grid gap-1.5 leading-none">
                    <label htmlFor="create-rule" className="text-sm font-medium cursor-pointer">Guardar como Gasto Recurrente</label>
                    <p className="text-xs text-zinc-500">Se crear치 una regla para recordarte este pago.</p>
                    {createRuleFromTx && (
                        <div className="flex items-center gap-2 mt-2 animate-in fade-in">
                            <Label className="text-xs whitespace-nowrap">D칤a del mes:</Label>
                            <Input type="number" min="1" max="31" className="w-16 h-8 bg-zinc-900 border-zinc-700 text-center" value={newRuleDay} onChange={e => setNewRuleDay(e.target.value)}/>
                        </div>
                    )}
                </div>
            </div>
        )}

        <Button type="submit" className={`w-full h-12 text-lg ${mode === 'income' ? 'bg-green-600' : mode === 'transfer' ? 'bg-blue-600' : 'bg-red-600'}`}>
          {mode === 'income' ? 'Siguiente' : mode === 'transfer' ? 'Siguiente' : 'Siguiente'} <ArrowRightLeft className="ml-2 h-4 w-4" />
        </Button>
      </form>
    </div>
  );
}