"use client";

import { useMemo, useState } from 'react';
import { Bar, BarChart, CartesianGrid, Legend, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts';
import { useFinance } from '@/context/FinanceContext';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';

export default function ComplexFinanceChart() {
  const { transactions } = useFinance();
  const [view, setView] = useState<'month' | 'year'>('month');
  
  // Obtenemos mes/a침o actual para filtrar por defecto
  const [selectedMonth, setSelectedMonth] = useState(new Date().getMonth().toString());

  const chartData = useMemo(() => {
    const today = new Date();
    const currentYear = today.getFullYear();
    const targetMonth = parseInt(selectedMonth);

    // VISTA MENSUAL: Mostrar D칤as (1-31)
    if (view === 'month') {
        // Crear array con d칤as del mes seleccionado
        const daysInMonth = new Date(currentYear, targetMonth + 1, 0).getDate();
        const data = Array.from({ length: daysInMonth }, (_, i) => ({
            name: (i + 1).toString(), // Eje X: 1, 2, 3...
            income: 0,
            expense: 0
        }));

        transactions.forEach(t => {
            const tDate = new Date(t.date);
            // Filtrar solo transacciones de este mes y a침o
            if (tDate.getMonth() === targetMonth && tDate.getFullYear() === currentYear) {
                const day = tDate.getDate() - 1; // 칈ndice array (d칤a 1 es indice 0)
                if (data[day]) {
                    if (t.type === 'income') data[day].income += t.amount;
                    if (t.type === 'expense') data[day].expense += t.amount;
                }
            }
        });
        return data;
    } 
    
    // VISTA ANUAL: Mostrar Meses (Ene-Dic)
    else {
        const data = [
            "Ene", "Feb", "Mar", "Abr", "May", "Jun", 
            "Jul", "Ago", "Sep", "Oct", "Nov", "Dic"
        ].map(m => ({ name: m, income: 0, expense: 0 }));

        transactions.forEach(t => {
            const tDate = new Date(t.date);
            if (tDate.getFullYear() === currentYear) {
                const month = tDate.getMonth();
                if (t.type === 'income') data[month].income += t.amount;
                if (t.type === 'expense') data[month].expense += t.amount;
            }
        });
        return data;
    }
  }, [transactions, view, selectedMonth]);

  // Formateador Pesos
  const formatYAxis = (value: number) => {
    return new Intl.NumberFormat('es-CO', { 
        style: 'currency', 
        currency: 'COP', 
        notation: "compact", 
        maximumFractionDigits: 1 
    }).format(value);
  };

  const formatTooltip = (value: number) => {
    return new Intl.NumberFormat('es-CO', { 
        style: 'currency', 
        currency: 'COP', 
        minimumFractionDigits: 0 
    }).format(value);
  };

  return (
    <Card className="bg-zinc-900 border-zinc-800">
      <CardHeader className="flex flex-row items-center justify-between pb-2">
        <CardTitle className="text-sm font-medium text-zinc-200">
            Resumen Financiero
        </CardTitle>
        <div className="flex gap-2">
            <Select value={view} onValueChange={(v:any) => setView(v)}>
                <SelectTrigger className="h-8 w-[100px] bg-zinc-950 border-zinc-700 text-xs">
                    <SelectValue />
                </SelectTrigger>
                <SelectContent>
                    <SelectItem value="month">Este Mes</SelectItem>
                    <SelectItem value="year">Este A침o</SelectItem>
                </SelectContent>
            </Select>
            
            {view === 'month' && (
                <Select value={selectedMonth} onValueChange={setSelectedMonth}>
                     <SelectTrigger className="h-8 w-[110px] bg-zinc-950 border-zinc-700 text-xs">
                        <SelectValue />
                    </SelectTrigger>
                    <SelectContent className="max-h-[200px]">
                        {Array.from({length: 12}).map((_, i) => (
                            <SelectItem key={i} value={i.toString()}>
                                {new Date(0, i).toLocaleString('es-CO', { month: 'long' })}
                            </SelectItem>
                        ))}
                    </SelectContent>
                </Select>
            )}
        </div>
      </CardHeader>
      <CardContent>
        <div className="h-[300px] w-full text-xs">
          <ResponsiveContainer width="100%" height="100%">
            <BarChart data={chartData} margin={{ top: 20, right: 10, left: 10, bottom: 0 }}>
              <CartesianGrid strokeDasharray="3 3" stroke="#333" vertical={false} />
              
              <XAxis 
                dataKey="name" 
                stroke="#666" 
                tickLine={false} 
                axisLine={false}
                // Si es mensual, agregamos "D" para indicar d칤a si hay espacio
                tickFormatter={(val) => view === 'month' ? `${val}` : val}
              />
              
              <YAxis 
                stroke="#666" 
                tickLine={false} 
                axisLine={false} 
                tickFormatter={formatYAxis}
                width={65} // <--- ANCHO EXTRA PARA NEGATIVOS
              />
              
              <Tooltip 
                contentStyle={{ backgroundColor: '#18181b', borderColor: '#27272a', borderRadius: '8px', color: '#fff' }}
                formatter={(value: number) => formatTooltip(value)}
                cursor={{ fill: 'rgba(255,255,255,0.05)' }}
              />
              
              <Legend wrapperStyle={{ paddingTop: '10px' }}/>
              
              <Bar dataKey="income" name="Ingresos" fill="#10b981" radius={[4, 4, 0, 0]} maxBarSize={40} />
              <Bar dataKey="expense" name="Gastos" fill="#ef4444" radius={[4, 4, 0, 0]} maxBarSize={40} />
            </BarChart>
          </ResponsiveContainer>
        </div>
      </CardContent>
    </Card>
  );
}